@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.container--calculator{flex-basis:75%;width:35%;display:flex;flex-direction:column;background-color:var(--black)}.container--calculator__input{width:100%;flex-basis:25%;background-color:#5a5a5a;border:none;font-size:2.5rem;color:white;padding-left:2.5rem;font-weight:lighter}.container--calculator__input:active,.container--calculator__input:focus{border:none}.container--calculator__control{width:100%;flex-basis:75%;display:flex}.container--calculator__control--left{flex-basis:75%;height:100%;display:flex;justify-content:space-around;flex-wrap:wrap;align-items:center}.container--calculator__control--left__button{flex-basis:35%;height:10%;display:flex;color:white;font-size:1.5rem;font-family:monospace;background-color:var(--light);display:flex;justify-content:center;align-items:center;transition:all .2s ease-in;font-weight:bold}.container--calculator__control--left__button:hover{background-color:var(--red);cursor:pointer}.container--calculator__control--right{flex-basis:25%;height:100%;display:flex;justify-content:space-around;align-items:center;flex-direction:column}.container--calculator__control--right__button{flex-basis:17.5%;width:100%;display:flex;justify-content:center;align-items:center;background-color:var(--light);color:white;font-size:1.5rem;font-family:monospace;font-weight:bold}.container--calculator__control--right__button:hover{cursor:pointer;background-image:linear-gradient(110deg, #c41438 0%, #ffaf2c 100%)}

.container--footer{color:var(--light);font-size:1.30rem}.container--header{flex-basis:5%;width:50%;display:flex;justify-content:center;align-items:center;color:#333331;font-size:2rem}

*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}body{box-sizing:border-box;font-family:'Roboto', sans-serif;outline:none}.container{width:100vw;height:100vh;display:flex;justify-content:space-around;align-items:center;background-color:#B2BEB8;background-color:var(--background);flex-direction:column}:root{--black: #1A1C1E;--red: #C41438;--light: #383A40;--background: #B2BEB8}

